  box-sizing: border-box; }

body {
  font-family: "TT Lakes", sans-serif;
  background-color: #F8F8F8;
  margin: 0;
  padding: 0; }
  @media (min-width: 1440px) {
    body {
      display: grid;
      grid-template-areas: "aside main" "aside foot";
      grid-template-rows: 1fr 120px;
      grid-template-columns: 320px 1fr; } }

body > .sidebar-left {
  grid-area: aside; }

body > main {
